:root{--blue:#3274d6;--charcoal:#666;--gray:#aab0b5;--green:#2bb772;--haze:#f7f8f8;--light:#f7f8f8;--mint:#d7f3d4;--navy:#1c3c5a;--red:#f3234a;--smoke:#d9dbdd;--yellow:#ffd246}body{-moz-osx-font-smoothing:grayscale;-ms-text-size-adjust:100%;-webkit-font-smoothing:antialiased;-webkit-text-size-adjust:100%;color:var(--navy);font-family:-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica,Arial,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol;font-size:16px;text-rendering:geometricPrecision}::-moz-selection{background:#ffebad}::selection{background:#ffebad}.container{max-width:100%;width:968px}@media screen and (max-width:968px){.container{padding-left:2em;padding-right:2em}}@media screen and (max-width:525px){.container{padding-left:1.2em;padding-right:1.2em}}.tsm{font-size:.7em;line-height:22px}.tmd{font-size:.85em;line-height:30px}.tlg{font-size:1.25em;line-height:38px}.divider{border-color:var(--smoke);border-top:none;height:1px}.navy{color:var(--navy)}.blue{color:var(--blue)}.bg-haze{background-color:var(--haze)}@media screen and (max-width:525px){.article-nav{display:none}.footer{flex-direction:column;padding-bottom:30px}.footer .copytext{margin-top:10px}}